import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionAuditLog , { ReduxEnumAuditLog }  from '../../../_redux/action/reduxAction.auditlog';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';

import { DateUtil }		                            from '../../../_util/util.date';








/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';











import './s.report.auditlog.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxAuditLog' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumAuditLog.INIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumAuditLog.SEARCH_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSearchDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentReportAuditLog extends ComponentAbstract {


            constructor( props ) {
                super( props )

                this.state = {

                    data : {
                        userType        : []
                        , resource      : []
                        , channel       : []
                        , action        : []
                    }


                    , form : {
                        userType        : ''
                        , resources     : ''
                        , userId        : ''
                        , username      : ''
                        , actions       : ''
                        , startDate     : ''
                        , endDate       : ''
                        , channel       : ''
                        , companyId     : ''
                        , bussName      : ''
                        , bussRegNum    : ''
                    }


                    , result : []

                }

            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestInit();
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestInit() {
                console.log( 'ComponentReportAuditLog : requestInit : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {

                                this.dispatch(
                                    ReduxActionAuditLog.rInit(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )

                            }
                        );

                    }
                )

            }
            requestInitDone( result ) {
                console.log( 'ComponentReportAuditLog : requestInitDone : *** : result => ' , result );


                let data = this.state.data;
                data[ 'userType' ]  = result[ 'userType' ];
                data[ 'channel' ]   = result[ 'channel' ];
                data[ 'action' ]    = result[ 'action' ];
                this.setState( {
                    isLoading   : false
                    , data      : data
                } );
                this.dispatch( ReduxActionAuditLog.rRequestEnd( ) );
            }




            requestSearch() {
                console.log( 'ComponentReportAuditLog : requestSearch : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionAuditLog.rSearch(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            userType        : this.state.form.userType
                                            , resources     : this.state.form.resources
                                            , userId        : this.state.form.userId
                                            , username      : this.state.form.username
                                            , actions       : this.state.form.actions
                                            , startDate     : this.state.form.startDate
                                            , endDate       : this.state.form.endDate
                                            , channel       : this.state.form.channel
                                            , companyId     : this.state.form.companyId
                                            , bussName      : this.state.form.bussName
                                            , bussRegNum    : this.state.form.bussRegNum
                                        }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestSearchDone( result ) {
                console.log( 'ComponentReportAuditLog : requestSearchDone : ***  : result => ' , result );

                this.setState(
                    {
                        isLoading : false
                        , result : result.auditLog
                    }
                );
                this.dispatch( ReduxActionAuditLog.rRequestEnd( ) );
            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>
                            <Paper style={ { width : '100%', margin : '0px auto' } }>
                                        
                                    <h3 style={ { paddingLeft: 25, paddingTop: 12 } }>Report - Audit Log</h3>

                                    <hr/>

                            <div style={ { paddingLeft: 25, paddingBottom: 10  } }>

                                <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                            
                                            <div style={ { flex: 0.15 } } >
                                                <p style={ { paddingTop: 32 } }>Resource</p>
                                            </div>

                                             <SelectField
                                                        style={ { flex: 0.25 } }
                                                        floatingLabelText="Resource"
                                                        value={ this.state.form.resources }
                                                        onChange={
                                                            ( event , index , value ) => {
                                                                let form = this.state.form;
                                                                form[ 'resources' ] = value;
                                                                this.setState( { form : form } );
                                                            }
                                                        } >
                                                <MenuItem   value={ 'ALL' } primaryText={ 'ALL' } />
                                                    {
                                                        this.state.data.resource.map( ( value , index ) => {
                                                            return ( <MenuItem  key={ '_report_auditlog_resources_' + index } value={ value.code }        primaryText={ value.label } />  );
                                                        } )
                                                    }
                                                </SelectField>

                                    </div>

                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                            
                                            <div style={ { flex: 0.15 } } >
                                                <p style={ { paddingTop: 32 } }>Channel</p>
                                            </div>

                                            <SelectField
                                                        style={ { flex: 0.25 } }
                                                        floatingLabelText="Channel"
                                                        value={ this.state.form.channel }
                                                        onChange={
                                                            ( event , index , value ) => {
                                                                let form = this.state.form;
                                                                form[ 'channel' ] = value;
                                                                this.setState( { form : form } );
                                                            }
                                                        } >
                                                <MenuItem   value={ 'ALL' }        primaryText={ 'ALL' } />
                                                {
                                                    this.state.data.channel.map( ( value , index ) => {
                                                        return ( <MenuItem  key={ '_report_auditlog_resources_' + index } value={ value.code }        primaryText={ value.label } />  );
                                                    } )
                                                }
                                            </SelectField>

                                    </div>


                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                            
                                            <div style={ { flex: 0.15 } } >
                                                <p style={ { paddingTop: 12 } }>User ID</p>
                                            </div>

                                            <TextField
                                                    style={ { flex: 0.25 } }
                                                    underlineFocusStyle={ { borderColor: '#000' } }
                                                    hintText={ "User ID" }
                                                    value={ this.state.form.userId }

                                                    onChange={
                                                        ( event , newValue ) => {
                                                            let form = this.state.form;
                                                            form[ 'userId' ] = newValue;
                                                            this.setState( { form : form } );
                                                        }
                                                    }

                                                />

                                    </div>


                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                            
                                            <div style={ { flex: 0.15 } } >
                                                <p style={ { paddingTop: 12 } }>User Name</p>
                                            </div>

                                             <TextField
                                                    style={ { flex: 0.25 } }
                                                    underlineFocusStyle={ { borderColor: '#000' } }
                                                    hintText={ "User Name" }
                                                    value={ this.state.form.username }

                                                    onChange={
                                                        ( event , newValue ) => {
                                                            let form = this.state.form;
                                                            form[ 'username' ] = newValue;
                                                            this.setState( { form : form } );
                                                        }
                                                    }

                                            />
                                    </div>

                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                            
                                            <div style={ { flex: 0.15 } } >
                                                <p style={ { paddingTop: 32 } }>Action</p>
                                            </div>

                                              <SelectField
                                                        style={ { flex: 0.25 } } 
                                                        floatingLabelText={ "Action" }
                                                        value={ this.state.form.actions }
                                                        onChange={
                                                            ( event , index , value ) => {
                                                                let form = this.state.form;
                                                                form[ 'actions' ] = value;
                                                                this.setState( { form : form } );
                                                            }
                                                        } >
                                                    <MenuItem   value={ 'ALL' }        primaryText={ 'ALL' } />
                                                    {
                                                        this.state.data.action.map( ( value , index ) => {
                                                            return ( <MenuItem  key={ '_report_auditlog_resources_' + index } value={ value.code }  primaryText={ value.label } />  );
                                                        } )
                                                    }
                                                </SelectField>
                                    </div>

                                        <div style={ { display: 'flex', paddingTop: 20, flexWrap: 'wrap' } }>
                                                   
                                                    
                                                   <p style={ { flex: 0.15, paddingTop: 12 } }>Date</p>
                                                   

                                                    <DatePicker
                                                            textFieldStyle={ { width: '100%' } }
                                                            style={ { flex: 0.25  } }
                                                            hintText="Start Date"
                                                            mode="landscape"

                                                            formatDate={
                                                                ( date ) => {
                                                                        return DateUtil.formatDate( DateUtil.objectToDate( date ) );
                                                                }
                                                            }

                                                            onChange={
                                                                ( nullValue , date ) => {
                                                                    let form = this.state.form;
                                                                    form[ 'startDate' ] = DateUtil.formatDateTimeStick( DateUtil.objectToDate( date ) );
                                                                    this.setState( { form : form } );
                                                                }
                                                            }
                                                    />


                                                   
                                                   <p style={ { flex: 0.05, paddingTop: 15, textAlign: 'center' } }>-</p>
                                                   

                                                    <DatePicker
                                                            textFieldStyle={ { width: '100%' } }
                                                            style={ { flex: 0.25  } }
                                                            hintText="End Date"
                                                            mode="landscape"

                                                            formatDate={
                                                                ( date ) => {
                                                                        return DateUtil.formatDate( DateUtil.objectToDate( date ) );
                                                                }
                                                            }

                                                            onChange={
                                                                ( nullValue , date ) => {
                                                                    let form = this.state.form;
                                                                    form[ 'endDate' ] = DateUtil.formatDateTimeStick( DateUtil.objectToDate( date ) );
                                                                    this.setState( { form : form } );
                                                                }
                                                            }
                                                />

                                       </div>

                                    </div>

                                    <hr/>

                                    <div style={ { paddingLeft: 25, paddingBottom: 10  } }>

                                        <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                                
                                                <div style={ { flex: 0.15 } } >
                                                    <p style={ { paddingTop: 12 } }>Company ID</p>
                                                </div>

                                                <TextField
                                                        style={ { flex: 0.25 } }
                                                        underlineFocusStyle={ { borderColor: '#000' } }
                                                        hintText={ "" }
                                                        value={ this.state.form.companyId }

                                                        onChange={
                                                            ( event , newValue ) => {
                                                                let form = this.state.form;
                                                                form[ 'companyId' ] = newValue;
                                                                this.setState( { form : form } );
                                                            }
                                                        }

                                                    />
                                        </div>

                                        <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                                
                                                <div style={ { flex: 0.15 } } >
                                                    <p style={ { paddingTop: 12 } }>Business Name</p>
                                                </div>

                                                <TextField
                                                    style={ { flex: 0.25 } }
                                                    underlineFocusStyle={ { borderColor: '#000' } }
                                                    hintText={ 'Business Name' }
                                                    value={ this.state.form.bussName }

                                                    onChange={
                                                        ( event , newValue ) => {
                                                            let form = this.state.form;
                                                            form[ 'bussName' ] = newValue;
                                                            this.setState( { form : form } );
                                                        }
                                                    }

                                                />
                                        </div>

                                         <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                                
                                                <div style={ { flex: 0.15 } } >
                                                    <p style={ { paddingTop: 12 } }>Business Registration No.</p>
                                                </div>

                                                <TextField
                                                        underlineFocusStyle={ { borderColor: '#000' } }
                                                        style={ { flex: 0.25 } }
                                                        hintText={ "" }
                                                        value={ this.state.form.bussRegNum }

                                                        onChange={
                                                            ( event , newValue ) => {
                                                                let form = this.state.form;
                                                                form[ 'bussRegNum' ] = newValue;
                                                                this.setState( { form : form } );
                                                            }
                                                        }

                                                    />
                                        </div>

                    

                                         <div style={ { display: 'flex' , justifyContent: 'flex-end', width: '40%', paddingTop: 30 } }>

                                                <div style={ { flex: 0.1 } } />
                                                
                                                <RaisedButton
                                                    style={ { flex: 0.2 } }
                                                    label={ "Search" }
                                                    onClick={ () => {
                                                        this.requestSearch();
                                                    } }
                                                   />

                                            
                                        </div>

                                    </div>

                                <hr/>


                                <Table
                                            fixedHeader={ false }
                                            selectable={ false }
                                            style={ { width: "100%" } }
                                            tableLayout="auto">

                                    <TableHeader
                                                    displaySelectAll={ false }
                                                    adjustForCheckbox={ false }>

                                        <TableRow>

                                            <TableHeaderColumn> Date </TableHeaderColumn>
                                            <TableHeaderColumn> Branch Name </TableHeaderColumn>
                                            <TableHeaderColumn> User ID </TableHeaderColumn>
                                            <TableHeaderColumn> User Name </TableHeaderColumn>
                                            <TableHeaderColumn> User Type </TableHeaderColumn>
                                            <TableHeaderColumn> Channel </TableHeaderColumn>
                                            <TableHeaderColumn> Company ID </TableHeaderColumn>
                                            <TableHeaderColumn> Business Name </TableHeaderColumn>
                                            <TableHeaderColumn> Business Registration Number </TableHeaderColumn>
                                            <TableHeaderColumn> Resource </TableHeaderColumn>
                                            <TableHeaderColumn> Actions </TableHeaderColumn>

                                        </TableRow>

                                    </TableHeader>

                                    <TableBody

                                                displayRowCheckbox={ false }>

                                        {
                                            this.state.result.map(
                                                ( item , index ) => {
                                                    return (
                                                        <TableRow
                                                                    key={ '_auditlog_record_' + index } >

                                                            <TableRowColumn>
                                                                <div className={ ' report-auditlog-textAlign-center ' }>
                                                                    { DateUtil.formatDate( DateUtil.stringToDate( item.dateTime ) ) }
                                                                </div>
                                                                <div className={ ' report-auditlog-textAlign-center ' }>
                                                                    { DateUtil.formatTime( DateUtil.stringToDate( item.dateTime ) ) }
                                                                </div>
                                                            </TableRowColumn>
                                                            <TableRowColumn> { item.branchName } </TableRowColumn>
                                                            <TableRowColumn>
                                                                <div> { item.userId } </div>
                                                                <div> { item.username } </div>
                                                                <div> { item.userType } </div>
                                                            </TableRowColumn>
                                                            <TableRowColumn> { item.channel } </TableRowColumn>
                                                            <TableRowColumn> { item.companyId } </TableRowColumn>
                                                            <TableRowColumn> { item.bussName } </TableRowColumn>
                                                            <TableRowColumn> { item.bussRegNum } </TableRowColumn>
                                                            <TableRowColumn> { item.resources } </TableRowColumn>
                                                            <TableRowColumn> { item.actions } </TableRowColumn>

                                                        </ TableRow>
                                                    )
                                                }
                                            )
                                        }

                                    </TableBody>

                                </Table>

                            </Paper>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />

                    </div>
                )
            }
        }

    )



